import { useState } from 'react';
import { signInWithEmailAndPassword, signInWithCustomToken } from 'firebase/auth';
import firebase from '../FirebaseConf';

const { auth } = firebase;

function LoginModal({ showModal, setShowModal, setIsLoggedIn }) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isSignUp, setIsSignUp] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (isSignUp) {
        const response = await fetch('/api/signup', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ email, password }),
        });
        if (!response.ok) {
          throw new Error('Помилка реєстрації');
        }
        const { idToken, uid } = await response.json();
        await signInWithCustomToken(auth, idToken);
        localStorage.setItem('userId', uid);
        setIsLoggedIn(true);
        alert('Користувача створено: ' + email);
      } else {
        const userCredential = await signInWithEmailAndPassword(auth, email, password);
        const idToken = await userCredential.user.getIdToken();
        const response = await fetch('/api/login', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ idToken }),
        });
        if (!response.ok) {
          throw new Error('Помилка входу');
        }
        const { uid } = await response.json();
        localStorage.setItem('userId', uid);
        setIsLoggedIn(true);
        alert('Успішний вхід: ' + email);
      }
      setShowModal(false);
      setEmail('');
      setPassword('');
    } catch (error) {
      alert('Помилка: ' + error.message);
    }
  };

  if (!showModal) return null;

  return (
      <div className="modal" style={{ display: 'flex' }}>
        <div className="modal-content">
          <span className="close" onClick={() => setShowModal(false)}>×</span>
          <h3>{isSignUp ? 'Реєстрація' : 'Увійти в систему'}</h3>
          <div id="auth-form" onSubmit={handleSubmit}>
            <label htmlFor="email">Електронна пошта:</label>
            <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
            />
            <label htmlFor="password">Пароль:</label>
            <input
                type="password"
                id="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
            />
            <button onClick={handleSubmit}>{isSignUp ? 'Зареєструватися' : 'Увійти'}</button>
            <button
                type="button"
                className="toggle-auth-btn"
                onClick={() => setIsSignUp(!isSignUp)}
            >
              {isSignUp ? 'Увійти' : 'Зареєструватися'}
            </button>
          </div>
        </div>
      </div>
  );
}

export default LoginModal;